/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "DM Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "DM Sans", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-amarillo {
  background-color: #E8641C;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #d55916;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E8641C;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #d55916;
  cursor: pointer;
}

.boton-verde {
  background-color: #005200;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #003900;
  cursor: pointer;
}

.dark-mode {
  background-color: black;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #9D9D9C;
}
.dark-mode .renta, .dark-mode .resumen-propiedad {
  background-color: #141414;
  border: 3px solid #90908f;
}
.dark-mode .renta .precio, .dark-mode .resumen-propiedad .precio {
  color: #005200;
}
.dark-mode .renta .icono, .dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #FFFFFF;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #575756;
  border: 2px solid #575756;
  color: #FFFFFF;
}

/** UI **/
.no-webp .inicio {
  background-image: url(../../build/img/imgfondo.jpg);
}

.webp .inicio {
  background-image: url(../../build/img/imgfondo.webp);
}

.header {
  background-color: #141414;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  font-size: 4rem;
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 1.5;
  max-width: 55rem;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    max-width: 80rem;
    font-size: 5rem;
    font-weight: 1000;
  }
}

.footer {
  padding: 0;
  background-color: #575756;
}
.footer .contenedor-footer {
  padding-top: 0;
}
@media (min-width: 768px) {
  .footer .navegacion1 {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
}
.footer .navegacion1 a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.3rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .footer .navegacion1 a {
    margin-right: 2rem;
    font-size: 1.3rem;
  }
  .footer .navegacion1 a:last-of-type {
    margin-right: 0;
  }
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  font-size: 1rem;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 4rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(0%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  visibility: visible;
  opacity: 0;
  height: 0;
  /*transition: visibility .3s ease-in-out, 
  opacity 0.7s linear;*/
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 3rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 3rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

/* Men√∫ de Hamburguesa */
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  margin-top: 3rem;
  padding-bottom: 0;
  width: 5rem;
}

.mostrar {
  /*display: flex;
  flex-direction: column;

  @include tablet{
      flex-direction: row;
  }
  */
  visibility: visible;
  opacity: 1;
  height: auto;
}

.alturaTemporal {
  height: auto;
}

.visibilidadTemporal {
  display: none;
}

.logo {
  width: 20rem;
  height: 7rem;
}

.titulo h1 {
  font-weight: 700;
  font-size: 6rem;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 6rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 3rem;
  margin-bottom: 0rem;
}

@media (min-width: 768px) {
  .contenedor-rentas {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.renta {
  background-color: #e9e9e9;
  border: 3px solid #90908f;
  margin-bottom: 2rem;
}
.renta .contenido-anuncio {
  padding: 2rem;
}
.renta .contenido-anuncio .ubicacion {
  font-weight: 1000;
  color: #E8641C;
  text-decoration: none;
}
.renta h3, .renta p {
  margin: 0;
}

.precio {
  color: #005200;
  font-weight: 1000;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 2;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 0rem;
  margin-right: 2rem;
}
.iconos-caracteristicas li p {
  font-weight: 1000;
  font-size: 2rem;
}

.titulo1 h2 {
  font-weight: 700;
  font-size: 3.4rem;
  margin-bottom: 0rem;
}

.resumen-propiedad .descripcion p {
  width: 8rem;
  height: 7rem;
  overflow: scroll;
}

.no-webp .imagen-contacto {
  background-image: url(../../build/img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(../../build/img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  margin: 0;
  font-weight: 1000;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 2rem;
  color: #FFFFFF;
  font-weight: 700;
}

.formulario p {
  font-size: 1.4rem;
  color: #575756;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #575756;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #9D9D9C;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

/** Paginas internas **/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 1000;
  font-size: 2.5rem;
  margin: 2rem 0 0 0;
  padding: 1rem 0 1rem 0;
}